    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive</span> <span class="hljs-attr">:appearance</span>=<span class="hljs-string">"appearance"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-instance-geometry</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">vc-geometry-rectangle</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"rectangle"</span> <span class="hljs-attr">:rectangle</span>=<span class="hljs-string">"rectangle"</span> <span class="hljs-attr">:vertexFormat</span>=<span class="hljs-string">"vertexFormat"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-geometry-rectangle</span>&gt;</span>
