        <span class="hljs-attr">appearanceOutline</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">vertexFormat</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">rectangle</span>: { <span class="hljs-attr">west</span>: <span class="hljs-number">110.5</span>, <span class="hljs-attr">south</span>: <span class="hljs-number">29.5</span>, <span class="hljs-attr">east</span>: <span class="hljs-number">115.5</span>, <span class="hljs-attr">north</span>: <span class="hljs-number">34.5</span> },
