<h2>属性</h2>
<h3><code v-pre>vc-geometry-rectangle</code></h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre>required</code> 指定矩形四至参数。<strong>结构：{ west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定矩形要缓存的顶点属性。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定矩形所在的椭球体。</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定每个经纬度之间的采样粒度。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定矩形高度。</td>
</tr>
<tr>
<td>rotation</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定矩形的旋转角（弧度），逆时针方向为正旋转。</td>
</tr>
<tr>
<td>stRotation</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定矩形的纹理旋转坐标（弧度）， 逆时针方向为正旋转。</td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定矩形拉伸高度。</td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre>vc-geometry-outline-rectangle</code></h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre>required</code> 指定矩形四至参数。<strong>结构：{ west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定矩形所在的椭球体。</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定每个经纬度之间的采样粒度。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定矩形高度。</td>
</tr>
<tr>
<td>rotation</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定矩形的旋转角（弧度），逆时针方向为正旋转。</td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定矩形拉伸高度。</td>
</tr>
</tbody>
</table>
<hr>
<p>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/RectangleGeometry.html">RectangleGeometry</a></strong>、<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/RectangleOutlineGeometry.html">RectangleOutlineGeometry</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          appearanceOutline: null,
          vertexFormat: null,
          rectangle: { west: 110.5, south: 29.5, east: 115.5, north: 34.5 },
          rectangleOutline: { west: 100.5, south: 29.5, east: 105.5, north: 34.5 },
          attributes: null
        }
      },
      mounted() {
        Promise.all([this.$refs.rectangle.createPromise, this.$refs.rectangleOutline.createPromise]).then((instances) => {
          console.log('All geometries are loaded.')
          const boundingSphereUnion = instances.reduce((prev, cur) => {
            const geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
            const boundingSphere = cur.vm.$parent.modelMatrix
              ? Cesium.BoundingSphere.transform(geometry.boundingSphere, cur.vm.$parent.modelMatrix)
              : geometry.boundingSphere
            return prev === null ? boundingSphere : Cesium.BoundingSphere.union(prev, boundingSphere)
          }, null)
          instances[0].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
        })
      },
      methods: {
        ready({ Cesium, viewer }) {
          const { MaterialAppearance, Material, Cartesian2, PerInstanceColorAppearance, ColorGeometryInstanceAttribute } = Cesium
          this.vertexFormat = MaterialAppearance.vertexFormat
          this.appearance = new MaterialAppearance({
            material: Material.fromType('Checkerboard', {
              repeat: new Cartesian2(20.0, 6.0)
            }),
            materialSupport: MaterialAppearance.MaterialSupport.TEXTURED
          })
          this.appearanceOutline = new PerInstanceColorAppearance({
            flat: true
          })
          this.attributes = {
            color: ColorGeometryInstanceAttribute.fromColor(Cesium.Color.RED.withAlpha(0.5))
          }
        }
      }
    }
  </script>